@import url("https://fonts.googleapis.com/css?family=DM+Sans:400,500,700&display=swap");

.App {
  height: 100vh;
  padding: calc(0px + 2vmin);
  font-family: "DM Sans", sans-serif;
  font-size: calc(10px + 1vmin);
  color: #1d1d20;

  display: flex;
  flex-direction: column;
}

.App-header {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: calc(10px + 2vmin);
}

img {
  width: 300px;
}

li {
  list-style: none;
}

input {
  font-family: "DM Sans", sans-serif;
  color: #b0aea5;
  padding: 5px 10px;
  border: 1px solid #eeeded;
  border-radius: 10px;
}

button {
  font-family: "DM Sans", sans-serif;
  color: #757575;
  background-color: white;
  padding: 5px 10px;
  border: 1px solid #eeeded;
  border-radius: 10px;
}

.multiple-value-text-input .multiple-value-text-input-item {
  background: white;
  padding: 5px 10px;
  line-height: 2.2em;
  border: 1px solid #eeeded;
  border-radius: 10px;
}

.search-limit {
  font-size: 0.8em;
  color: #b0aea5;
  text-align: right;
  padding: 0;
  line-height: 0;
}

.results {
  text-align: center;
}
/* 
.higher {
  color: #7681f0;
